body {
  display: flex;
  flex-direction: row;
  background: linear-gradient(
    to bottom,
    rgb(55, 153, 219),
    rgb(179, 200, 228),
    rgb(255, 255, 255)
  );
  width: 100%;
  height: 100%;
  margin: 0%;
}

/* right side */

canvas {
  width: 50%;
  height: 100%;
  position: fixed;
  right: 0;
  top: 0;
  outline: none;
}

canvas.change {
  width: calc(100% - 80px);
}

/* left side */

aside {
  width: 50%;
  height: 100vh;
  overflow: hidden;
  position: relative;
  transform: translateX(0px);
  transition: all 800ms cubic-bezier(0.8, 0, 0.33, 1);
  background-color: aliceblue;
  box-shadow: 10px 0px 15px rgb(177, 179, 182);
}

aside.change {
  transform: translateX(calc(-50vw + 80px));
  border-radius: 0% 0% 0% 0%;
  transition: all 800ms cubic-bezier(0.8, 0, 0.33, 1);
}

aside .menu-btn {
  position: absolute;
  top: 30px;
  right: 3%;
  padding: 0;
  width: 40px;
  cursor: pointer;
  z-index: 2;
  color: lightgray;
}

svg {
  margin: 0 5px;
}

.line {
  padding: 0;
  width: 35px;
  background: rgb(0, 0, 0);
  height: 5px;
  margin: 5px 0;
  transition: all 700ms cubic-bezier(0.9, 0, 0.33, 1);
  border-radius: 2px;
}

.line1 {
  transform: rotate(-40deg);
}

.line2 {
  transform: scaleX(1.2) translateX(10px);
}

.line3 {
  transform: rotate(40deg);
}

.change .line1 {
  transform: rotate(0deg);
}

.change .line2 {
  transform: scaleX(1) translateX(0px);
}

.change .line3 {
  transform: rotate(0deg);
}

aside .nav-links {
  position: absolute;
  width: 90%;
  height: 10%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  transition: all 700ms cubic-bezier(0.9, 0, 0.33, 1);
}

.change .nav-links {
  position: absolute;
  margin-top: 10%;
  height: 55%;
  display: flex;
  flex-direction: column;
  align-items: baseline;
  justify-content: space-evenly;
  transform: translateX(calc(50vw - 70px));
}

.change .nav-links a {
  color: transparent;
}

.change .nav-links a svg {
  color: black;
  height: 30px;
  width: 30px;
}

.link {
  margin: 20px 10px;
  text-decoration: none;
  font-family: sans-serif;
  color: rgba(65, 65, 65, 0.9);
  font-weight: 700;
  text-transform: uppercase;
  font-size: 1rem;
}

.link:hover {
  color: rgb(0, 0, 0);
}

#textDiv {
  width: 90%;
  height: 70%;
  padding: 5%;
  margin-top: 10%;
  margin-bottom: 10%;
  overflow-y: scroll;
  direction: rtl;
  margin-right: 100px;
}

.articleDiv {
  width: 90%;
  margin: auto;
  direction: ltr;
  /* display: none; */
}

.articleDiv.active {
  width: 90%;
  margin: auto;
  direction: ltr;
  display: block;
}

h1 {
  color: lightgray;
  font-size: 100px;
  margin-bottom: -50px;
}

.link.active {
  color: black;
  size: 20px;
  font-weight: 1000;
  border-bottom: 3px solid rgb(44, 44, 44);
}

.footer {
  top: 90%;
  bottom: 0;
  width: 80%;
  position: absolute;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  transition: all 700ms cubic-bezier(0.9, 0, 0.33, 1);
}

.change .footer {
  top: 55%;
  width: auto;
  height: 35%;
  flex-direction: column;
  align-items: baseline;
  justify-content: space-evenly;
  transform: translateX(calc(50vw - 70px));
}

/*Scrollbar*/
/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

@media only screen and (max-width: 768px) {
  h1 {
    font-size: 50px;
    margin-bottom: -20px;
  }

  canvas {
    width: 100%;
    height: 80%;
    top: 10%;
    position: fixed;
    left: 0;
  }

  aside {
    width: 100%;
    height: 100vh;
    flex-direction: column;
    z-index: 1;
    background-color: transparent;
  }

  aside.change {
    width: 100%;
    height: 2%;
    transform: none;
  }

  aside .menu-btn {
    position: fixed;
    top: 30px;
    right: 3%;
    padding: 0;
    width: 40px;
    cursor: pointer;
    z-index: 2;
    color: lightgray;
  }

  aside .nav-links {
    flex-direction: column;
    height: auto;
    width: 100%;
  }

  aside.change .nav-links {
    transform: none;
    height: 2%;
  }

  .change .nav-links a svg {
    color: transparent;
    height: 30px;
    width: 30px;
  }

  .line1 {
    transform: rotate(-45deg);
  }

  .line2 {
    opacity: 0;
  }

  .line3 {
    transform: translateY(-20px) rotate(45deg);
  }

  .change .line2 {
    opacity: 1;
  }

  .change .line3 {
    transform: none;
  }

  #textDiv {
    background-color: rgba(128, 128, 128, 0.25);
    padding: 0;
    margin: 0;
    width: 100%;
    direction: ltr;
  }

  .nav-bar {
    height: 100%;
    width: 100%;
    background-color: aliceblue;
  }

  .footer {
    flex-direction: column;
    top: 60%;
    height: 25%;
    margin-bottom: 5%;
  }

  .change .footer {
    background-color: transparent;
    height: 0;
  }
}
